html,
body,
.site-wrapper {
  height: 100%;
}

body {
  background-color: #f8f8f8;
  font-family: "Roboto", sans-serif;
  color: #595959;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333;
  font-weight: normal;
}

a {
  color: #55595c;
}

a:hover,
a:focus {
  color: #6d7377;
  text-decoration: none;
  outline: none;
}

mark,
.mark {
  background-color: #efefef;
  border-radius: 5px;
  color: #333;
  padding: 0.2em 0.4em;
  margin: 0 1px;
}

code,
.code {
  background-color: #333;
  color: #ffde00;
}

:active,
:focus,
:hover {
  outline: 0;
}

.text-default {
  color: #333 !important;
}

.site-header {
  background-color: #ffde00;
  height: 60px;
  left: 0;
  padding: 12px 15px;
