  border-collapse: collapse;
  table-layout: auto;
  margin-bottom: 0;
}

.table-component__table__caption {
  position: absolute;
  top: auto;
  left: -10000px;
  overflow: hidden;
  width: 1px;
  height: 1px;
}

.table-component__table th,
.table-component__table td {
  padding: 0.75em 1.25em;
  vertical-align: top;
  text-align: left;
}

.table-component__message {
  color: #999;
  text-align: center;
  padding-top: 10px;
}

.table-component__th--sort,
.table-component__th--sort-asc,
.table-component__th--sort-desc {
  cursor: pointer;
  user-select: none;
}

.table-component__th--sort-asc:after,
.table-component__th--sort-desc:after {
  position: absolute;
  left: 0.25em;
  display: inline-block;
  color: #bbb;
}

.table-component__th--sort-asc:after {
  content: "";
}

.table-component__th--sort-desc:after {
  content: "";
}

.table-component .pagination {
  justify-content: flex-end !important;
  margin-top: 10px;
}

.table-component .pagination .page-item {
  overflow: hidden;
}

.table-component .pagination .page-item.active .page-link {
  color: #fff !important;
}

.table-component .pagination a i {
  display: block;
  padding: 0.5rem 0.75rem;
  margin-left: -1px;
  line-height: 1.25;
  background-color: #fff;
  border: 1px solid #dee2e6;
  font-style: normal;
  cursor: pointer;
}

.table-component .pagination a.disabled i {
  color: #d9d9d9;
  pointer-events: none;
  cursor: auto;
}

.hamburger {
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}

.hamburger-box {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  left: 4.5px;
  right: 4.5px;
  margin-top: -2px;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  height: 2px;
  background-color: #fff;
  border-radius: 2px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
  width: 100%;
}

.hamburger-inner::before {
  top: -5px;
}

.hamburger-inner::after {
  bottom: -5px;
}

.hamburger--arrowturn.is-active .hamburger-inner {
  transform: rotate(-180deg);
}

.hamburger--arrowturn.is-active .hamburger-inner::before {
  transform: translate3d(3px, 1px, 0) rotate(45deg) scale(0.7, 1);
}

.hamburger--arrowturn.is-active .hamburger-inner::after {
  transform: translate3d(3px, -1px, 0) rotate(-45deg) scale(0.7, 1);
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

.gauge {
  position: relative;
  display: flex;
  align-items: center;
}

.gauge-lg .basic-gauge,
.gauge-lg .donut-gauge,
.gauge-lg .color-gauge {
  height: 250px;
}

.gauge-lg .gauge-label {
  font-size: 40px;
}

.gauge-md .basic-gauge,
.gauge-md .donut-gauge,
.gauge-md .color-gauge {
  height: 180px;
}

.gauge-sm .basic-gauge,
.gauge-sm .donut-gauge,
.gauge-sm .color-gauge {
  height: 120px;
}

.gauge-sm .gauge-label {
  font-size: 20px;
}

.gauge-label {
  display: inline-block;
  position: absolute;
  top: 60%;
  left: 0px;
  width: 100%;
  text-align: center;
  font-size: 28px;
  font-weight: bold;
}

.gauge-label.middle-label {
  top: 50%;
  transform: translateY(-50%);
}

.gauge {
  position: relative;
}

@media (max-width: 480px) {
  .gauge-lg .basic-gauge,
  .gauge-lg .donut-gauge,
  .gauge-lg .color-gauge {
    height: 180px !important;
    width: auto !important;
  }

  .gauge-lg .gauge-label {
    font-size: 28px;
  }
}

